import { getInTheaters,  getNewMovie, getTop250 } from '../../apis/index.js'
export default {
	data() {
		return {
			start: 0,
			count: 8,
		    typelist: ["即将上映电影","豆瓣电影新片榜","豆瓣电影Top250"],
			num: -1,
			list: [],
			flag: false
		}
	},
	async onLoad(option){
		 this.num = this.typelist.indexOf(option.title)
		 let res;
		 uni.showLoading({
		 	"title": "加载中......"
		 })
		if(this.num == 0){
			 res = await getInTheaters({
				start: this.start,
				count: this.count,
				city: this.$store.state.city
			})
			console.log(this.$store.state.city)
		}else if(this.num == 1){
			 res = await getNewMovie({
				start: this.start,
				count: this.count,
				city: this.$store.state.city
			})
		}else if(this.num == 2){
			 res = await getTop250({
				start: this.start,
				count: this.count,
				city: this.$store.state.city
			})
		}
		this.list = res
		if(this.list.length != 0){
			uni.hideLoading()
		}
		 console.log(this.list, res, 5)
	},
	methods:{
		async toMuch(){
			this.start += 10;
			console.log(this.start)
			let res;
			let ress =  await uni.showLoading({
			 	"title": "加载中......",
				mask: true
			 })
			 console.log(ress)
			if(!this.flag){
				if(this.num == 0){
					 res = await getInTheaters({
						start: this.start,
						count: this.count,
						city: this.$store.state.city
					})
					this.flag = !this.flag
				}else if(this.num == 1){
					 res = await getNewMovie({
						start: this.start,
						count: this.count,
						city: this.$store.state.city
					})
					this.flag = !this.flag
				}else if(this.num == 2){
					 res = await getTop250({
						start: this.start,
						count: this.count,
						city: this.$store.state.city
					})
					this.flag = !this.flag
				}
				if(this.flag){
					uni.hideLoading()
					this.flag = !this.flag
				}
			}
		}
	}
}
